// --- Logic for home.html ---
if (window.location.pathname.includes('home.html')) {
    const dailyTaskText = document.getElementById('daily-task-text');
    const streakCircles = document.querySelectorAll('.streak-circle');
    const usePassBtn = document.getElementById('use-pass-btn');
    const passCountDisplay = document.getElementById('pass-count-display');
    const refreshTaskBtn = document.getElementById('refresh-task-btn');
    const settingsBtn = document.getElementById('settings-btn');
    const dropdownMenu = document.getElementById('dropdown-menu');
    const logoutBtn = document.getElementById('logout-btn');
    const taskPassedOverlay = document.querySelector('.task-passed-overlay');
    const streakCounter = document.querySelector('.streak-counter');
    const streakCountSpan = document.getElementById('streak-count');

    // ... (deedsByCategory remains the same) ...

    let userState = JSON.parse(localStorage.getItem('userState')) || {
        passCount: 3,
        streak: Array(7).fill(null), // null, 'completed', or 'passed'
        lastPassDate: null,
        streakCount: 0
    };

    const saveState = () => localStorage.setItem('userState', JSON.stringify(userState));
    const today = new Date();
    const todayString = today.toDateString();
    const dayOfWeek = today.getDay() === 0 ? 6 : today.getDay() - 1; // Pzt=0, Sal=1... Paz=6

    // ... (getDailyTask and displayDailyTask remain the same) ...

    const updateUI = () => {
        passCountDisplay.textContent = `(${userState.passCount})`;
        streakCountSpan.textContent = userState.streakCount;
        streakCircles.forEach((circle) => {
            const dayIndex = parseInt(circle.dataset.day, 10);
            const dayStatus = userState.streak[dayIndex];
            circle.classList.toggle('completed', dayStatus === 'completed');
            circle.classList.toggle('passed', dayStatus === 'passed');
        });
        if (userState.streak[dayOfWeek] === 'passed') {
            taskPassedOverlay.classList.add('active');
            document.getElementById('prove-task-btn').disabled = true;
            refreshTaskBtn.disabled = true;
            usePassBtn.disabled = true;
        }
    };

    const animateStreak = () => {
        streakCounter.classList.add('animate');
        setTimeout(() => streakCounter.classList.remove('animate'), 500);
    };

    const showAd = () => {
        alert("Reklam gösteriliyor...");
        return true;
    };

    if (usePassBtn) {
        usePassBtn.addEventListener('click', () => {
            if (userState.lastPassDate === todayString) {
                alert("Bugün zaten bir pas hakkı kullandın.");
                return;
            }
            if (userState.passCount > 0) {
                if (showAd()) {
                    userState.passCount--;
                    userState.streak[dayOfWeek] = 'passed';
                    userState.lastPassDate = todayString;
                    userState.streakCount++;
                    saveState();
                    updateUI();
                    animateStreak();
                    alert("Pas hakkı kullanıldı!");
                }
            } else {
                alert("Hiç pas hakkın kalmadı!");
            }
        });
    }
    
    streakCircles.forEach(circle => {
        circle.addEventListener('click', () => {
            // This is a simulation of completing a task
            const dayIndex = parseInt(circle.dataset.day, 10);
            if(userState.streak[dayIndex] === null) { // Can only complete if not already passed/completed
                 userState.streak[dayIndex] = 'completed';
                 userState.streakCount++;
                 saveState();
                 updateUI();
                 animateStreak();
            }
        });
    });

    // ... (rest of the event listeners) ...

    displayDailyTask();
    updateUI();
}